/** ---------------------------- */
/** --- Code editor ------------ */
/** ---------------------------- */
.code-edit-container {
    position: relative;
    height: 80vh;
    width: 30vw;
    border: 1px solid hsl(0, 0%, 60%);
    background-color: hsl(212, 35%, 95%);
    margin: 1em 0;
}

.code-input,
.code-output {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 2rem);
    height: calc(100% - 2rem)   ;
    padding: 1rem;
    border: none;
    font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
    background: transparent;
    white-space: pre-wrap;
    line-height: 1.5em;
    word-wrap: break-word;
    font-size: 0.8rem;
}

.code-input {
    opacity: 1;
    margin: 0;
    color: hsl(0, 0%, 40%);
    resize: none;
}

.code-output {
    pointer-events: none;
    z-index: 3;
    margin: 0;
    overflow-y: auto;
}

code {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 1rem;
    display: block;
    color: hsl(0, 0%, 40%);
    font-size: 0.8rem;
    font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
}

/* overrides */
.code-edit-container :not(pre) > code[class*="language-"],
.code-edit-container pre[class*="language-"] {
    background: transparent;
    margin: 0;
}