.item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: absolute;
    cursor: pointer;
    /* border: black solid 1px; */
    box-sizing: border-box;
}


.group-item{
    position: absolute;
    box-sizing: border-box;
}

.item-available{
    border: lightgray solid 1px;
}

.item-selected{
    border: yellowgreen solid 1px;
}

.controller{
    display: flex;
    flex-direction: row;
}

.print{
    display: flex;
    flex-direction: row;
}



.xml-input{
    min-width: 40em;
    min-height: 80vh;
}


.control-panel{
    display: flex;
    flex-direction: column;
    padding: 1em;
}

.data-editor{
    padding: 1em;
    max-height: 80vh;
    overflow: scroll;
}

.group-block{
    display: flex;
    flex-direction: column;

}

